<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>&nbsp;&nbsp;<i class="el-icon-lx-lock"></i>&nbsp;&nbsp;&nbsp;资讯动态
        </el-breadcrumb-item>
        <el-breadcrumb-item>编辑</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" label-width="160px">
          <el-form-item label="标题" prop="title" :label-width="formLabelWidth">
            <el-input v-model="form.title"  autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="图片" class="padding" prop="img" :label-width="formLabelWidth">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreview"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="imageUrl" :src="img+imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="概况" prop="info" :label-width="formLabelWidth">
            <el-input type="textarea"  v-model="form.description" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="详情" prop="description" :label-width="formLabelWidth">
            <UE :defaultMsg=defaultMsg :config=config ref="ue"></UE>
          </el-form-item>
          <!--          </el-form-item>-->
          <el-form-item class="submit">
            <el-button type="primary" @click="onSubmit('form')">提交</el-button>
            <el-button @click="$router.go(-1)">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'
  import UE from '../../common/uediter/ue.vue'

  export default {
    name: 'articleD',
    data: function () {
      return {
        img:window.HOST+'/',
        imageUrl: '',
        masterImg: "",
        uploadUrl: window.HOST + "/admin/upfile",
        token: '',
        form: {},
        id: '',
        dialogVisible: false,
        dialogImageUrl: '',
        formLabelWidth: '100px',
        config: {
          initialFrameWidth: 831,
          initialFrameHeight: 350
        },
        defaultMsg: '',
        defaultMsg2: '',
      }
    },
    components: {
      UE
    },
    created() {

    },
    activated(){
      this.getV()
    },
    methods: {
      getV() {
        var that = this;
        var params = {
          'datoken': that.$route.query.datoken,
        };
        that.apiPost('/admin/djwzone', params).then((res) => {
          this.handelResponse(res, (data) => {
            that.form = data.data;
            that.imageUrl = that.form.minspic;
            that.$nextTick(() => {
              that.defaultMsg = data.data.info;
              // that.defaultMsg2 = data.data.description;
              that.$refs.ue.setContent(that.form.info);
              // that.$refs.ue2.setContent(that.form.description);
            })
          })
        })
      },
      //上传图片
      handlePictureCardPreview(file) {
        this.form.imgUrl = file.url;
        this.imageUrl = file.url;
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = res.data;
        this.form.imgUrl = res.data;
        // this.imageUrl = this.form.bPic;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type == 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;

        /*if(!isJPG) {
        	this.$message.error('上传正确格式图片!');
        }*/
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过 2MB!');
        }
        return isLt2M;
      },
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if(valid) {
            var that = this;
            var params = {
              title:that.form.title,
              description:that.form.description,
              info:that.$refs.ue.getUEContent(),
              datoken:that.form.datoken,
              minspic:that.imageUrl,
              type:that.form.type
            };
            that.apiPost('/admin/djwzupdate', params).then((res) => {
              this.handelResponse(res, (data) => {
                that.$message.success('成功');
                that.$router.go(-1);
              })
            })
          } else {
            return false;
          }
        });

      }
    },
    mixins: [http]
  }
</script>
<style>
  .el-upload--text {
    width: 180px !important;
  }
  .el-upload--text img {
    width: 100%;
    height: 100%;
  }
