  /*  border-radius: 6px;*/
  /*  cursor: pointer;*/
  /*  position: relative;*/
  /*  overflow: hidden;*/
  /*}*/
  /*.avatar-uploader .el-upload:hover {*/
  /*  border-color: #409EFF;*/
  /*}*/
  /*.avatar-uploader-icon {*/
  /*  font-size: 28px;*/
  /*  color: #8c939d;*/
  /*  width: 500px;*/
  /*  height: 178px;*/
  /*  line-height: 178px;*/
  /*  text-align: center;*/
  /*}*/
  /*.el-upload--text{*/
  /*  width: auto;*/
  /*  height:auto ;*/
  /*  border: none;*/
  /*}*/
  /*.avatar {*/
  /*  width: 500px;*/
  /*  height: 178px;*/
  /*  display: block;*/
  /*}*/
</style>



